body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color{
  background: #833ab4;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
}

.landing-grid{
  background: #833ab4;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
  text-align: center;
  position: fixed;
  top: 10;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img{
  height: 250px;
  margin-top: 5em;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;  
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;  
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i:hover{  
  opacity: 0.4;
  -webkit-transform: scale(1.2,1.2);
          transform: scale(1.2,1.2);
}
.social-links i{
  color: white;
  font-size: 5em;
  padding: 8px;
}

/* Project page css */
.projects-grid {    
  display: flex;  
  flex-flow: row wrap;
  justify-content: flex-start;
}

.projects-grid .card{
margin-bottom: 25px !important;    
 height: 272px !important;
}

.projects-grid .card a{
  -webkit-text-decoration: "none" !important;
          text-decoration: "none" !important;
}
/* Project page css */

/* Contacts page css */
.contact-body{
  margin: auto;  
  /* position: fixed; */
  text-align: center;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width: 100%;    
  background: white;
  color: black;  
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: "Anton",sans-serif;
}

.contact-grid p{
  font-family: "Oxygen",sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  margin: auto;
  width: 50%;
}

.contact-list i{  
  font-size: 66px;
  padding-right: 5rem;
}
/* Contacts page css */

/* Resume page css */
.resume-right-col{
  background: #393235;
  color: white;
  padding: 2em;
}

.resume-right-col a{
  text-decoration: none;  
  margin: 10px;
}

.resume-right-col a:hover{  
  color:lightblue;
  opacity: 0.6;
}

.resume-left-col a{
  text-decoration: none;
  color:#00f;
}

.resume-left-col a:hover{  
  opacity: 0.4;
}
/* Resume page css */
@media only screen and (max-width: 800px) {
  .landing-grid{
    /*position: static;*/
  }
  .social-links{
    justify-content: space-around;
  }
  .banner-text h1{
    font-size: 36px;        
  }
  .social-links i{    
    font-size: 3em;
    padding: 5px;
  }
  .projects-grid{
    flex-direction: column;    
  }  
}
